.publications-card {
  background-color: #434343;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  padding: 20px;
  margin: 20px;
  text-align: left;
  transition: transform 0.3s ease-in-out;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border:30px;
  position: relative;
  margin-bottom: 30px;
  
  
}
.year-heading{
  font-size: 2rem;
  margin-top: 50px;
  padding-top: 20px;
}

.publications-card:hover {
  transform: translateY(-5px);
  transform: scale(1.04);
}

.publication-info {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  width: calc(100% - 150px); /* Adjust the width according to your requirement */
}

.publication-content {
  flex: 1;
}


.publications-title {
  font-size: 1.8em;
  color: #E69A8DFF;
  margin-bottom: 10px;
}

.authors {
  font-style: italic;
  color: var(--color5);
  margin-bottom: 8px;
}

.journal {
  color: var(--color4);
}

.volume {
  color: var(--color4);
}

.year {
  position: absolute;
  margin-top:30px;
  top: -55px; /* Adjust the top position to move the year subheading above the card */
  left: 20px; /* Adjust the left position to align the year subheading to the left */
  font-size: 1.2em;
  color:#fff;
}

.publication-image {
  width: 150px; /* Adjust the width according to your design */
  height: auto;
  border-radius: 8px;
  margin-right: 20px; /* Adjust spacing between image and content */
}

.publications-link {
  
  padding: 10px 15px;
  background-color: #4E9F3D;
  color: var(--color3);
  text-decoration: none;
  border-radius: 5px;
  transition: background-color 0.3s ease-in-out;
}
.publications-link:hover {
  background-color: var(--color4);
}

.media-item{
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
}
td{
  padding: 20px;
}
.media-logo{
  width: 100px; 
  height: 100px; 
  border-radius: 50%; 
  border: 2px solid #ccc; 
  transition: transform 0.4s ease-in-out; 

}


@media (max-width: 768px) {
  .publications-card {
    margin: 20px; /* Reduce margin for smaller screens */
  }
  
  .publication-info {
    flex-direction: column; /* Stack items vertically */
  }

  .publication-image {
    width: 100%; /* Make image full width for smaller screens */
    margin-right: 0; /* Remove right margin for smaller screens */
    margin-bottom: 20px; /* Add bottom margin for spacing */
  }
  .year{
    top:-50px;
  }
}