/* FormComponent.css */

.form-container {
  max-width: 80%;
  margin: 10px 90px 30px 90px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #535559;
  border-radius: 10px;
  border: 1px solid #000000;
  box-shadow: 5px 10px #282626; 
}

.form-container form {
  width: 80%; /* Ensure form takes full width of container */
}

.form-container input,
.form-container button {
  width: 100%; 
  max-width: 800px; 
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  box-sizing: border-box;
}


@media (max-width: 1200px) {
  .form-container input,
  .form-container button {
    max-width: 600px; /* Adjust max-width for smaller screens */
  }
}

@media (max-width: 992px) {
  .form-container input,
  .form-container button {
    max-width: 600px; /* Adjust max-width for medium screens */
  }
}

@media (max-width: 768px) {
  .form-container input,
  .form-container button {
    max-width: 500px; /* Adjust max-width for smaller devices */
  }
}


#report{
  text-align: center;
  margin-bottom: 20px;
  margin-top: 100px;
  color: #fff;
}

.form-container form {
  display: flex;
  flex-direction: column;
}

.form-container input[type="text"],
.form-container input[type="email"],
.form-container input[type="file"] {
  margin-bottom: 15px;
  padding: 10px;
  border-radius: 5px;
  border: 1px solid #ccc;
  outline: none;
}

.form-container input[type="text"]:focus,
.form-container input[type="email"]:focus,
.form-container input[type="file"]:focus {
  border-color: #6dabe4;
}

.form-container button[type="submit"] {
  padding: 10px;
  border-radius: 5px;
  border: none;
  background-color: #e69a8dff;
  color: #fff;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.form-container button[type="submit"]:hover {
  background-color: rgb(3, 194, 3);
}
